@import-normalize;
/*
 * Aliases
 */
/*
 * Background colors
 *
 * Note: in some cases, components can have other background colors (header-top, file, footer...)
 * These are the main background colors.
 */
html {
  box-sizing: border-box;
  font-size: 93.75%; }
  @media (min-width: 768px) {
    html {
      font-size: 112.5%; } }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  text-rendering: optimizelegibility; }

[aria-hidden='true'] {
  display: none; }

img {
  height: auto;
  max-width: 100%; }

/**
 * Icons
 */
@font-face {
  font-family: 'EuropaIcons';
  src: url("../fonts/europa-icons/europa-icons.eot");
  src: url("../fonts/europa-icons/europa-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/europa-icons/europa-icons.woff") format("woff"), url("../fonts/europa-icons/europa-icons.ttf") format("truetype"), url("../fonts/europa-icons/europa-icons.svg#europa-icons") format("svg"); }

.ecl-icon::before {
  display: inline-block;
  font-family: 'EuropaIcons';
  /* stylelint-disable-line font-family-no-missing-generic-family-keyword */
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  margin-right: 4px;
  speak: none;
  text-decoration: none;
  text-transform: none; }

.ecl-icon--after {
  display: inline-block;
  font-family: 'EuropaIcons';
  /* stylelint-disable-line font-family-no-missing-generic-family-keyword */
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  margin-left: 4px;
  speak: none;
  text-decoration: none;
  text-transform: none; }

