/* stylelint-enable */
.ecl-heading {
  margin: 0 0 1.66rem;
  padding: 0; }

.ecl-heading--h1 {
  color: #000;
  font-size: 2.22rem;
  font-weight: 600;
  line-height: 1.1; }

.ecl-heading--h2 {
